/* The container must be positioned relative: */
.mg-dropdown {
  position: relative;
  margin-bottom: 1rem;
  display: inline-block;
}
/*hide original SELECT element: */
.mg-dropdown input {
  display: none; 
}

.mg-dropdown--content {
  width: 100%;
  min-width: max-content;
  box-shadow: $control-shadow; 
  margin-left: auto;
  margin-right: auto;
  border: 1px solid;
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: none;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  background-color: $color-initial;
  top: 100%;
  z-index: 999;
  border-color: $color-quaternary;
  border-radius: $input-radius;
  max-height: 50vh;
  max-width: 90vw;
  overflow-y: auto;
  overflow-x: hidden;

} 
 
.opened + .mg-dropdown--content {
  display: block;
  opacity: 1;
  visibility: visible;
} 
.mg-dropdown--icon { 
  //force padding to container element to reserve space for arrow
   padding-right: 2.5rem!important;
    
  &::after {
    @include mg-inline-css-icon; 
    @include mg-dropdown--icon; 
  } 
 
  &.opened::after {
    transform: rotate(-225deg);
    top: 40%;   
  }
      
} 
 