.mg-container {
  width: 100%;
  max-width: 1025px;
  margin-left: auto;
  margin-right: auto;
  padding-left: .8rem;
  padding-right: .8rem;
}

.mg-container-fluid {
  max-width: width auto;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: .8rem;
  padding-right: .8rem;
}

.mg-row {
  flex-flow: wrap;
  flex: 0 auto;
  display: flex;
}

.mg-dropdown {
  margin-bottom: 1rem;
  display: inline-block;
  position: relative;
}

.mg-dropdown input {
  display: none;
}

.mg-dropdown--content {
  box-shadow: var(--mg-control-shadow);
  opacity: 0;
  visibility: hidden;
  background-color: var(--mg-color-initial);
  z-index: 999;
  border: 1px solid;
  border-color: var(--mg-color-quaternary);
  border-radius: var(--mg-input-radius);
  width: 100%;
  min-width: max-content;
  max-width: 90vw;
  max-height: 50vh;
  margin-left: auto;
  margin-right: auto;
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: none;
  position: absolute;
  top: 100%;
  overflow: hidden auto;
}

.opened + .mg-dropdown--content {
  opacity: 1;
  visibility: visible;
  display: block;
}

.mg-dropdown--icon {
  padding-right: 2.5rem !important;
}

.mg-dropdown--icon:after {
  vertical-align: middle;
  box-sizing: border-box;
  content: "";
  pointer-events: none;
  transform-origin: center;
