.mg-collapse {
  justify-content: space-between;
  align-items: center;
  display: inline-flex;
}

.mg-collapse + .mg-collapse--content {
  display: none;
}

.mg-collapse.opened + .mg-collapse--content {
  display: block;
}

.mg-collapse--icon {
  position: relative;
  padding-right: 2.5rem !important;
}

.mg-collapse--icon:after {
  vertical-align: middle;
  box-sizing: border-box;
  content: "";
  pointer-events: none;
  transform-origin: center;
  border-top: 0;
  border-bottom: 2px solid;
  border-left: 2px solid;
  border-right: 0;
  width: .8rem;
  height: .8rem;
  transition: all .2s;
  display: inline-block;
  position: absolute;
  top: calc(50% - .4rem);
  right: 1rem;
  transform: rotate(-135deg);
}

.mg-collapse--icon.opened:after {
  top: 40%;
  transform: rotate(-45deg);
}

.mg-icon {
  box-sizing: content-box;
  font-size: inherit;
  text-indent: -9999px;
  vertical-align: middle;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 2rem;
  height: 2rem;
  margin-left: .2rem;
  margin-right: .2rem;
  font-style: normal;
  display: inline-block;
  position: relative;
}

.mg-icon:before, .mg-icon:after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mg-icon.mg-icon--s {
  width: 1.16rem;
  height: 1.16rem;
  font-size: 1.16rem;
}

